<template>
  <div class="container top">
    <div class="detaibox">

      <p class="title">{{article.title}}</p>
      <p class="subtitle">{{article.subtitle}}</p>

        <ul class="photolist">
          <li v-for="(value,index) in article.photolist" :key="index">
            <img :src="value.imgurl">
            <p>{{value.text}}</p>
          </li>

        </ul>

      <button class="more" @click="goIndex">更多照片</button>
    </div>
  </div>
</template>

<script>
import slider from '@/components/Slider'

export default {
  name: 'PhotoDetail',
  data () {
    return {
      slider: false,
      article: {
        title: '标题',
        subtitle: '即使是无所不能的Pointing也无法控制盲文，这几乎是一种非正式的生活。但是有一天Lorem Ipsum的一小段盲文决定离开远方的语法世界。',
        photolist: [
          {imgurl: 'https://s1.ax1x.com/2018/09/07/iC2kOP.jpg', text: '即使是无所不能的Pointing也无法控制盲文，这几乎是一种非正式的生活。但是有一天Lorem Ipsum的一小段盲文决定离开远方的语法世界。'},
          {imgurl: 'https://s1.ax1x.com/2018/09/07/iC2kOP.jpg', text: '即使是无所不能的Pointing也无法控制盲文，这几乎是一种非正式的生活。但是有一天Lorem Ipsum的一小段盲文决定离开远方的语法世界。'}
        ]
      }
    }
  },
  components: {
    slider
  },
  methods: {
    goIndex () {
      this.$router.push({name: 'photos'})
    }
  }
}
</script>

<style scoped lang="stylus">
.top
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 300;
  font-size: 20px;
  line-height: 1.6;
  color: rgba(0, 0, 0, 0.5)
.detaibox
  padding:.5rem
  .title
    font-size: .45rem
    color: #000;
    font-family: "Merriweather", Times, serif;
    font-weight: 700;
    margin-bottom:.3rem
  .subtitle
    font-size: .2rem
    margin-bottom:1.5em
.photolist
  img
    width: 100%
    margin-bottom: .2rem
    height:auto
  p
    padding:0 .4rem .45rem
    font-size: .2rem
.more
  background: transparent;
  color: #7ac143;
  border: 2px solid #7ac143;
  transition: 0.3s;
  border-radius: .5rem;
  padding: 8px 20px
  margin:0 auto
  display: block
  font-size:.2rem
  margin-bottom: 1rem

.more:hover
  background:#7ac143;
  color: white
</style>



